import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import classes from './ContentList.module.css';

class ContentList extends Component {
  state = {
    content: ['Projects', 'Skills', 'Articles', 'Open Source']
  };

  render() {
    const { content } = this.state;
    return (
      <ul>
        {content.map(item => {
          const path = 'portfolio/' + item.toLowerCase().replace(' ', '');
          return (
            <li className={classes.ContentList} key={item}>
              <Link className={classes.Link} to={`/${path}`}>{item}</Link>
            </li>
          );
        })}
      </ul>
    );
  }
}

export default ContentList;
